import { FaFacebookSquare, FaInstagramSquare, FaSafari } from "react-icons/fa";
import classes from './Contact.module.css'
import {motion} from 'framer-motion'

const handleURL = (url) => {
    return () => window.open(url, "blank")
}


const Contact = () => {
    return ( 
        <div className={classes.Contact} id="contact">

            
            <div className={classes.ContactIcons}>

                <motion.div
                    whileHover={{scale:1.3}}
                >
                    <FaFacebookSquare color='white' size='30px' style={{padding:'1%'}}
                    onClick={handleURL('https://www.facebook.com/rivergumcc/')} />
                </motion.div>

                <motion.div
                    whileHover={{scale:1.3}}
                >
                    <FaInstagramSquare color='white' size='30px' style={{padding:'1%'}}
                    onClick={handleURL('https://instagram.com/rivergumcc?utm_medium=copy_link')} />
                </motion.div>  

                <motion.div
                 whileHover={{scale:1.3}}
                >
                    <FaSafari color='white' size='30px' style={{padding:'1%'}}
                    onClick={handleURL('http://rivergumcc.vic.cricket.com.au/')} />
                </motion.div> 

                
                    

            </div>
        </div>


     );
}
 
export default Contact ;