import classes from './Home.module.css'
import squid from './../../img/squid.svg'
import Links from '../Links/Links'
import binaryWorld from './../../img/binaryWorld.svg'
import kookas from './../../img/kookas.svg'
import Sponsors from '../Sponsors/Sponsors'
import Contact from '../Contact/Contact'
import SPDLOGO from './../../img/SPDLOGO500.png';
import gryffin from './../../img/gryffin500.jpg';

import Squid from './Squid' 
import {motion} from 'framer-motion'

const Home = () => {
    return ( 
        <div className={classes.Home}>
            <div className={classes.FlexContainer}>
                
                <div className={classes.Heading}>
                <h1 className={classes.Hello}>Rivergum Cricket Club</h1>
                </div>
                
            
            
                <div className={classes.squidSvg}>
                    <Squid  />
                    
                </div>
            
            
            
                <div className={classes.LinksContainer}>
                <Links />
                
                </div>

                <div className={classes.Sponsors}>
                     

                </div>

                <div>
                    <h2>
                        Major Sponsors
                    </h2>


                </div>



                <div className={classes.FlexTest}>
                   
                
                   <div className={classes.SPD}>
                        <a href="https://spdelectrics.com.au/" target="_blank">
                            <div className={classes.name}>
                        
                                <div>
                                    <p>SPD ELECTRICS</p>
                                </div>

                                <div>
                                    <img  src={SPDLOGO} alt="SPD LOGO" />
                                </div>

                            </div>
                        </a>
                    </div>
                

                    <div className={classes.gryffin}>
                        <a href="https://instagram.com/gryffin.group?utm_medium=copy_link" target="_blank">
                                <div className={classes.name}>

                                    <div>
                                        <p>GRYFFIN GROUP</p>
                                    </div>

                                    <div>
                                        <img src={gryffin} alt="Gryffin Group" />
                                    </div>
                                </div>
                        </a>
                    </div>
                
                
                
                
                </div>




                

                <div className={classes.Contacts}>
                <Contact />
                </div>


            </div>
        </div>


     );
}
 
export default Home;



